.feedback__buttons {
    display: flex;
    flex-direction: row;
    padding: 0px;
    justify-content: space-around;
}

.feedback__btn {
    width: 150px;
    height: 40px;
    padding: 0px 10px 0px 10px;
    border-radius: 5px;
    font-size: 16px;
    font-weight: 700;
    text-transform: uppercase;
}

.feedback__btn:hover {
    cursor: pointer;
}
